$purple: var(--color-bg);
$l-purple: var(--color-bg);
$t-purple: var(--color-text);
$pink: #FF5E65;
$white: var(--color-text);

.boxGhost {
    margin-top: 40px;
    padding: 15px 25px 25px;
    position: relative;
    width: 200px;
    // left: 50%;
    // top: 30%;
    // transform: translate(-50%, -30%);
}

.boxGhostShadow {
    height: 20px;
    box-shadow: 0 50px 15px 5px var(--color-text);
    border-radius: 50%;
    margin: 0 auto;
    animation: smallnbig 3s ease-in-out infinite;
}

.boxGhostEyes {
    position: absolute;
    left: 50%;
    top: 45%;
    //transform: translate(-50%, -45%); 
    height: 12px;
    width: 70px;
}

.boxEyeLeft {
    width: 12px;
    height: 12px;
    background: $l-purple;
    border-radius: 50%;
    margin: 0 10px;
    position: absolute;
    left: 0;
}

.boxEyeRight {
    width: 12px;
    height: 12px;
    background: $l-purple;
    border-radius: 50%;
    margin: 0 10px;
    position: absolute;
    right: 0;
}

.boxGhostContainer {
    background: $white;
    width: 100px;
    height: 100px;
    border-radius: 100px 100px 0 0;
    position: relative;
    margin: 0 auto;
    animation: upndown 3s ease-in-out infinite;
}

.boxGhostBottom {
    display: flex;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;

    div {
        flex-grow: 1;
        position: relative;
        top: -10px;
        height: 20px;
        border-radius: 100%;
        background-color: $white;

        &:nth-child(2n) {
            top: -12px;
            margin: 0 -0px;
            border-top: 15px solid $l-purple;
            background: transparent;
        }
    }
}

.symbol {
    &:nth-child(1) {
        opacity: .2;
        animation: shine 4s ease-in-out 3s infinite;

        &:before,
        &:after {
            content: '';
            width: 12px;
            height: 4px;
            background: $white;
            position: absolute;
            border-radius: 5px;
            bottom: 65px;
            left: 0;
        }

        &:before {
            transform: rotate(45deg);
        }

        &:after {
            transform: rotate(-45deg);
        }
    }

    &:nth-child(2) {
        position: absolute;
        left: -5px;
        top: 30px;
        height: 18px;
        width: 18px;
        border: 4px solid;
        border-radius: 50%;
        border-color: $white;
        opacity: .2;
        animation: shine 4s ease-in-out 1.3s infinite;
    }

    &:nth-child(3) {
        opacity: .2;
        animation: shine 3s ease-in-out .5s infinite;

        &:before,
        &:after {
            content: '';
            width: 12px;
            height: 4px;
            background: $white;
            position: absolute;
            border-radius: 5px;
            top: 5px;
            left: 40px;
        }

        &:before {
            transform: rotate(90deg);
        }

        &:after {
            transform: rotate(180deg);
        }
    }

    &:nth-child(4) {
        opacity: .2;
        animation: shine 6s ease-in-out 1.6s infinite;

        &:before,
        &:after {
            content: '';
            width: 15px;
            height: 4px;
            background: $white;
            position: absolute;
            border-radius: 5px;
            top: 10px;
            right: 30px;
        }

        &:before {
            transform: rotate(45deg);
        }

        &:after {
            transform: rotate(-45deg);
        }
    }

    &:nth-child(5) {
        position: absolute;
        right: 5px;
        top: 40px;
        height: 12px;
        width: 12px;
        border: 3px solid;
        border-radius: 50%;
        border-color: $white;
        opacity: .2;
        animation: shine 1.7s ease-in-out 7s infinite;
    }

    &:nth-child(6) {
        opacity: .2;
        animation: shine 2s ease-in-out 6s infinite;

        &:before,
        &:after {
            content: '';
            width: 15px;
            height: 4px;
            background: $white;
            position: absolute;
            border-radius: 5px;
            bottom: 65px;
            right: -5px;
        }

        &:before {
            transform: rotate(90deg);
        }

        &:after {
            transform: rotate(180deg);
        }
    }
}

//keyframes
@keyframes upndown {
    0% {
        transform: translateY(5px);
    }

    50% {
        transform: translateY(15px);
    }

    100% {
        transform: translateY(5px);
    }
}

@keyframes smallnbig {
    0% {
        width: 90px;
    }

    50% {
        width: 100px;
    }

    100% {
        width: 90px;
    }
}

@keyframes shine {
    0% {
        opacity: .2;
    }

    25% {
        opacity: .1;
    }

    50% {
        opacity: .2;
    }

    100% {
        opacity: .2;
    }
}